
export default function sketch(p){
      let canvas;

      var cols, rows;
      var scl = 40;
      var w = 2.5*p.windowHeight;
      var h = 1.5*p.windowHeight;

      var flying = 0;

      var terrain = [];


      p.setup = () => {
            canvas = p.createCanvas(p.windowWidth, p.windowHeight, p.WEBGL);
            cols = w / scl;
            rows = h/ scl;
            p.background(0, 1);
            p.fill(0)

            for (var x = 0; x < cols; x++) {
                  terrain[x] = [];
                  for (var y = 0; y < rows; y++) {
                        terrain[x][y] = 0; //specify a default value for now
                  }
            }
      }
  
      p.draw = () => {
            p.stroke('#b08338'); // #4A675A #ACB5A0 #3A4A51
            p.strokeWeight(3);
            p.fill('#2c2a2b')
            p.background('#2c2a2b');
            flying -= 0.003;
            var yoff = flying;

            for (var y = 0; y < rows; y++) {
                  var xoff = 0;
                  for (var x = 0; x < cols; x++) {
                        terrain[x][y] = p.map(p.noise(xoff, yoff), 0, 1, -200, 150);
                        xoff += 0.1;
                  }
                  yoff += 0.1;
            }

            p.background('#2c2a2b');
            p.translate(-150, 140);
            p.rotateX(p.PI/1);
            p.rotateY(p.PI/1.9);
            // p.rotateZ(p.PI/0);
            p.translate(-w/30, -h/3, -h/1000);

            for (var y = 0; y < rows-1; y++) {
                  p.beginShape(p.TRIANGLE_STRIP);
                  for (var x = 0; x < cols; x++) {
                        p.vertex(x*scl, y*scl, terrain[x][y]);
                        p.vertex(x*scl, (y+1)*scl, terrain[x][y+1]);
                  }
                  
                  p.endShape();
            }
      }
  

      p.windowResized = () => {
            p.resizeCanvas(p.windowWidth, p.windowHeight);
      }
  }